'use strict';

/**
 * The is a local modified version of the clone npm module (https://www.npmjs.com/package/clone).
 * According to its description, it offers a foolproof deep cloning 
 * of objects, arrays, numbers, strings, maps, sets, 
 * promises, etc. in JavaScript.
 * 
 * I refactored it to be compatible with Webpack.
 * Refator List:
 * - update the use of the 'var' keyword to 'let' and 'const' where appropriate.
 * - Made the variable names more clearer and intuitive (clean code)
 * - I used ES6 contants/definitions where possible.
 * 
 * Original can be found Here:
 * - On Npm -> https://www.npmjs.com/package/clone
 * - On GitHub -> https://github.com/pvorb/node-clone
 * 
 * Created by oodigie on 26/05/2023.
 */
